.taskbar {
    background-color: #053C5E;
    display: grid;
    grid-template-columns: 20% 20% 40% 20%;
    place-items: center;
    height: 15%;
    z-index: 1;
    padding: 10px;
}

.word-preview {
    height: 50%;
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    margin: auto;
    width: 100%;
}

.word-preview:focus {
    outline: none;
}

.btn {
    background-color: #1f7a8c;
    margin: 0 10px; /* Adjust margin to space buttons */
    padding: 10px 20px;
    border-radius: 5px;
    border: 5px solid white;
    font-weight: bold;
    font-size: 16px;
    font-family: inherit;
    color: inherit;
    text-shadow: inherit;
    box-shadow: 4px 4px 0px black;
    letter-spacing: 0.05rem;
    width: 80%;
    height: 75%;
}

.btn:hover {
    background-color: #1A6675;
}

.btn:active {
    background-color: #165764;
}

.submit-btn {
    background-color: #DB222A;
}

.submit-btn:hover {
    background-color: #C21E26;
}

.submit-btn:active {
    background-color: #B01C23;
}

/* Media Query for Mobile Devices */
@media (max-width: 768px) {
    .taskbar {
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        /* height: 15% */
    }

    .word-preview {
        width: 90%;
        margin-bottom: 10px;
        grid-column: 1 / -1;
        order: -1;
        height: 50%;
    }

    .btn {
        margin: 10px 0;
        padding: 5px 10px;
        height: 65%;
    }
}
