.game {
    text-align: center;
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.top-section {
    display: flex;
    /* flex: 0 0 100%; */
    height: 85%;
}

.game-section {
    width: 50vw;
    z-index: 1;
}

/* Media Query for Mobile Devices */
@media (max-width: 768px) {

    .game {
        height: 100vh;
    }
    .top-section {
        flex-direction: column;
        background-color: #bfdbf7;
        /* height: 85%; */
        /* margin-top: auto; */
    }
    
    .game-section {
        /* margin-top: auto; */
        height: 60%;
        width: 100vw;
    }
}