.letter {
    /* border: 2px solid black;
    margin-top: 0px;
    margin-bottom: 0px;
    padding: 10px;
    font-weight: bold;
    font-size: 40px; */
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 12vh;
    font-weight: bold;
    padding: 10px;
    border: 2px solid black;
    background-color: #1F7A8C;
    color: #FFFFFF;
    border-top: 2px solid #28A0B8;
    border-right: 2px solid #124954;
    border-bottom: 2px solid #124954;
    border-left: 2px solid #28A0B8;
    user-select: none;

}

.letter:hover {
    background-color: #1A6675;
    color: #ffffffde;
}

.letter:active, .selected, .letter:hover.selected {
    background-color: #165764;
    color: #ffffffcb;
}

.top-border {
    border-top: 12px solid#124954;
    text-align: center;
    font-size: 11vh;
}

.right-border {
    border-right: 12px solid #28A0B8;
    text-align: center;
    font-size: 11vh;
}

.bottom-border {
    border-bottom: 12px solid #28A0B8;
    text-align: center;
    font-size: 11vh;
}

.left-border {
    border-left: 12px solid #124954;
    text-align: center;
    font-size: 11vh;

    .letter-value {
        font-size: 18px;

    }
}

.top-border > .letter-value, .right-border > .letter-value, .bottom-border > .letter-value, .left-border > .letter-value {
    font-size: 16px;
}

.all-border {
    border-top: 12px solid #124954;
    border-right: 12px solid #28A0B8;
    border-bottom: 12px solid #28A0B8;
    border-left: 12px solid #124954;
    font-size: 10vh;
}

.all-border > .letter-value {
    font-size: 12px;
}

.letter-value {
    position: absolute;
    bottom: 2px;
    right: 2px;
    font-size: 20px;
    margin: 0px;
}

@media (max-width: 768px) {
    .letter {
        font-size: 8vh;
    }
    .top-border, .left-border, .right-border, .bottom-border {
        font-size: 7vh;
    }
    .all-border {
        font-size: 5.5vh;
    }
    .letter > .letter-value {
        font-size: 14px;
    }
    .top-border > .letter-value, .left-border > .letter-value, .right-border > .letter-value, .bottom-border > .letter-value {
        font-size: 13px;
    }
    .all-border  > .letter-value {
        font-size: 12px;
    }
}