.letter-grid {
    display: grid;
    grid-template-columns: repeat(5, 20%);
    grid-template-rows: repeat(5, 20%);
    height: 81%;
    /* width: 60vw; 
    /* height: 60vw; */
    border: 15px solid #A31621;
}

@media (max-width: 768px) {
    .letter-grid {
        height: 80%;
        /* width: 70%; */
        margin: auto;
    }
}