.main {
    background-color: #BFDBF7;
    width: 50%;
    /* margin: 10px; */
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    /* justify-content: center; */
}

.main-title {
    background-color: #1F7A8C;
    color: #FFFFFF;
    border: 5px solid #A31621;
    /* display: inline; */
    width: 300px;
    margin: 30px auto;
    border-radius: 5px;
    text-shadow: 3px 3px 0px black;
    box-shadow: 5px 5px 0px black;
    letter-spacing: 0.1rem;
}

.accepted-words {
    margin: 0 20px;
    width: 90%;
    background-color: #1F7A8C;
    color: #FFFFFF;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 20% 20% 20% 20% 20%;
    border: 5px solid #A31621;
    border-radius: 5px;
    box-shadow: 5px 5px 0px black;
}

.word-entry {
    margin: 10px 10px;
    border-bottom: 1px solid #053C5E;
    padding-bottom: 2px;
    font-size: 16px;
    font-weight: bold;
    height: 16px;
}

.icon-container {
    /* position: absolute; */
    /* align-self: flex-start; */
    background-color: #FFFFFF;
    width: 125px;
    height: 125px;
    border-radius: 50%;
    /* flex: 0 0 125px; */
    /* margin-top: 100px; */
    /* margin-left: 30px; */
    border: 5px solid #053C5E;
}

.mascot-icon {
    position: relative;
    width: 80px;
    top: 25px;
    right: 3px;
    /* z-index: 0; */
    /* border-radius: 42%; */
}

.owl-and-hints-container {
    display: flex;
    margin-top: 65px;
    align-items: center;
    width: 90%;
    justify-content: center;
}

.hint-area {
    background-color: white;
    padding: 20px 0px;
    width: 50%;
    height: 80px;
    /* line-height: 80px; */
    border: 5px solid #053C5E;
    margin: 5px;
    border-radius: 5px;
    color: black;
    text-shadow: none;
    text-align: center;
    flex: 0 0 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

@keyframes fadeOut {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

.fade-out {
    animation: fadeOut 0.2s forwards;
    animation-delay: 3s;
}

.mascot-message {
    color: #053C5E;
    font-size: 18px;
    font-weight: bold;
    /* text-align: left; */
    margin: 10px;
}

.red {
    color: #db222a;
}

@media (max-width: 768px) {
    .main {
        width: 100%;
        height: 40%;
        align-items: center;
    }

    .main-title {
        margin: 10px;
        margin-top: auto;
    }

    .accepted-words {
        height: 30%;
        margin-top: auto;
        width: 90%;
        grid-template-rows: 50% 50%;
        grid-template-columns: 20% 20% 20% 20% 20%;
    }

    .word-entry {
        font-size: 10px;
        padding-bottom: 1px;
        margin: 10px 2px;
    }

    .icon-container {
        width: 50px;
        height: 50px;
    }

    .owl-and-hints-container {
        margin-top: auto;
        margin-bottom: 5px;
    }

    .mascot-icon {
        width: 50%;
        position: relative;
        top: 13px;
        right: 1px;
    }

    .hint-area {
        height: 10px;
    }

    .mascot-message {
        font-size: 12px;
    }
}