.scoreboard {
    background-color: #053C5E;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 15%;
}

.scoreboard-entry {
    background-color: #1f7a8c;
    /* margin: 20px; */
    height: 30%;
    padding: 10px 20px;
    border-radius: 5px;
    /* line-height: 16px; */
    /* padding-bottom: 10px; */
    border: 5px solid white;
    box-shadow: 4px 4px 0px black;
}
@media (max-width: 768px) {
    .scoreboard-entry {
        padding: 5px 10px;
    }
}
